<template>
    <div v-if="ready">
         <form @submit.prevent="createList">
            <div class="card">
                <div class="card-header" style='height:73px!important;position:relative'>
                    <div class='flex'>
                        <strong>{{ display_title() }}</strong>
                    </div>
                    <div class="tabs">
                        <div class="flex">
                            <div :class="['leftTab subTabs',{'activeTab':mode=='view'},{'disableTab':mode=='gen'}]" @click="select_mode('view')">
                                <u>View Stocktake List</u>
                            </div>
                            <div :class="['rightTab subTabs',{'activeTab':mode=='gen'},{'disableTab':mode=='view'}]" @click="select_mode('gen')">
                                <u>Generate Stocktake List</u>
                            </div>
                        </div>
                    </div>              
                </div>
                <div class="card-body card-block" style='padding:0 0 0 0'>
                    <!--View Stocktake list-->
                    <div v-if="mode=='view'">
                        <div class="rowWrapper">
                            <div class="col-12">
                                <v-card-title class='px0'>
                                    <div class="col-6 px0 flex" style='padding-top:15px'>
                                        <div class="col-8 px0">
                                            <select class="form-control select" v-model="selected_record" @change="fetch_stock_take_list">
                                                <option value='' disabled>Choose record below</option>
                                                <option v-for="rs in record_list" :value="rs.id">{{ rs.dp }}</option>
                                            </select>
                                        </div>
                                        <div class="col-4">
                                            <select class="form-control select" v-model="view_mode">
                                                <option value='all'>All Records</option>
                                                <option value='completed'>Checked Records</option>
                                                <option value='pending'>Pending Records</option>
                                            </select>
                                        </div>                                       
                                    </div>
                                <v-spacer></v-spacer>
                                <v-text-field
                                    v-model="key"
                                    append-icon="search"
                                    label="Search"
                                    single-line
                                    hide-details
                                ></v-text-field>
                                </v-card-title>
                                <v-data-table
                                    :search="key"
                                    :headers="headers"
                                    :items="datalist"
                                    :rows-per-page-items="[10]"
                                    class="elevation-1"                
                                    disable-initial-sort
                                    show-select              
                                    item-key="item_code"               
                                >          
                                <template v-slot:items="props">                     
                                    <tr style='cursor:pointer' v-if="checkview(props.item.stocktake)">
                                        <td class="click_row">{{ props.item.item_code }}</td>
                                        <td class="click_row">{{ props.item.barcode }}</td>
                                        <td class="click_row">{{ props.item.item_eng_name }}</td>
                                        <td class="click_row">{{ props.item.building_code+' > '+props.item.location_code+' > '+props.item.sub_location_code }}</td>
                                        <td class="click_row">
                                            <select class="form-control select" v-model="props.item.stocktake" :disabled="props.item.stocktake==true"  @change="updateItem(props.item)">
                                                <option :value="true">Checked</option>
                                                <option :value="false">Pending</option>
                                            </select>
                                        </td>
                                    </tr>    
                                    <template v-slot:pageText="props">
                                        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                                    </template>  
                                </template>
                                </v-data-table>
                            </div>
                        </div>
                    </div>
                    <!--[END]View Stocktake list-->
                    <!--Generate Stocktake list-->
                    <div v-if="mode=='gen'">
                        <div class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Description
                                </div>    
                                <div class="col-8">
                                    <input type="text" class="form-control" v-model="description" required placeholder="Enter Description"/>
                                </div>
                            </div>
                        </div>
                        <div class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Section
                                </div>    
                                <div class="col-8">
                                    <select class="form-control select" v-model="section" @change="load_filter" required>
                                        <option value=''>All</option>
                                        <option value="S">Secondary</option>
                                        <option value="P">Primary</option>
                                        <option value="K">Kindergarten</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Group
                                </div>    
                                <div class="col-8">
                                    <select class="form-control select" v-model="grpcode" @change="count_items" required>
                                        <option value=''>All</option>
                                        <option v-for="grp in grplist" :value="grp.groupcode">{{ grp.group_description }}</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Type
                                </div>    
                                <div class="col-4 flex">
                                    <input type="radio" value="full" class="form-control" v-model="checktype" @click="resetCount" :checked="item_count<11"/>
                                    <label>Full Stock Take<label>
                                </div>
                                <div class="col-4 flex">
                                    <input type="radio" value="part" class="form-control" v-model="checktype"/>
                                    <label>Partial Take<label>
                                </div>
                            </div>
                        </div>
                        <div v-if="checktype=='part'" class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Percentage (%)
                                </div>    
                                <div class="col-8">
                                    <input type="number" step="1" min="0" max="100" class="form-control" v-model="percentage" @change="count_items"/>
                                </div>
                            </div>
                        </div>
                        <div class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Item Count
                                </div>    
                                <div class="col-8">
                                    <input type="number" step="1" min="0" max="100" class="form-control" v-model="item_count" readonly/>
                                </div>
                            </div>
                        </div>
                        <div class="rowWrapper">
                            <div class="flex">
                                <div class="col-4">
                                    Duration
                                </div>    
                                <div class="col-8">
                                    <div class="flex" style='margin-bottom:1rem'>
                                        <div class="col-4 px0">
                                            <input type="date" class="form-control" v-model="period.start" readonly required/>
                                        </div>
                                        <div class="col-4"></div>
                                        <div class="col-4 px0">
                                            <input type="date" class="form-control" v-model="period.end" readonly required/>
                                        </div>
                                    </div>
                                    <VueCtkDateTimePicker 
                                        :noHeader="true"
                                        :noKeyboard="true"
                                        :buttonColor="'rgb(237,28,37)'" 
                                        :color="'rgb(237,28,37)'" 
                                        :formatted="'ll'"                    
                                        :format="'YYYY-MM-DD'" 
                                        :id="'DatePicker'" 
                                        :label="'Select Date Range'"
                                        :noButtonNow="true"
                                        :range="true"
                                        :inline="true"
                                        v-model="period" 
                                    >                            
                                    </VueCtkDateTimePicker>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!--[END]Generate Stocktake list-->
                </div>
                <div class="card-footer" v-if="mode=='view'">
                    <button type="button" class="btn btn-primary btn-sm"  @click="export_list">
                        <i class="fa fa-file-excel-o"></i> Export
                    </button>
                    <button type="button" class="btn btn-danger btn-sm" @click="return_pg">
                        <i class="fa fa-undo"></i> Return
                    </button>            
                </div>
                <div class="card-footer" v-if="mode=='gen'">
                    <button type="submit" class="btn btn-primary btn-sm" >
                        <i class="fa fa-paper-plane"></i> Submit 
                    </button>
                    <button type="button" class="btn btn-danger btn-sm" @click="return_pg">
                        <i class="fa fa-undo"></i> Return
                    </button>            
                </div>
            </div>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return {
            key:'',
            view_mode:'all',
            headers: [
                { text: 'Item Code',value :'item_code',sortable: true},
                { text: 'Barcode',value:'barcode',sortable: true},
                { text: 'Name',value:'item_eng_name',sortable: true},                
                { text: 'Location',value:'sub_location_code',sortable: true},
                { text: 'Status',value:'stocktake',sortable: true},
            ],
            datalist:[],
            record_list:[],
            selected_record:'',
            description:'',
            period:{
                start:'',
                end:''
            },
            mode:'view',
            grplist:[],
            grpcode:'',
            percentage:100,
            item_count:0,
            section:'',
            checktype:'full',
            ready:false,
        }
    },
    async created(){
        await this.load_filter();
        await this.fetch_stocktake_records();
        this.ready = true;
    },
    methods:{
        async updateItem(item){
            var perform = false;
            var msg = "Confirm stocktake ["+item.item_code+']'+item.item_eng_name+'?';
            this.$confirm(msg).then(
                async ()=>{
                    var formData = new FormData();
                    formData.append('item_code',item.item_code);
                    await axios.post("inventory/web/backend/updateStocktake.php",formData)
                        .then(
                            (response)=>{
                                item.stocktake = true;
                                perform = true;
                            }
                        )
                }
            )
            if(perform==false){
                item.stocktake = false;
            }
        },
        export_list(){
            if(this.selected_record!=''){
                var formData = new FormData();
                formData.append('rid',this.selected_record);
                axios.post("inventory/web/backend/export_takelist.php",formData)
                    .then(
                        (response)=>{
                            const url = window.URL.createObjectURL(new Blob([response.data]));
                            const link = document.createElement('a');
                            link.href = url;
                            link.setAttribute('download', 'StocktakeRecord_'+this.selected_record+'.csv'); //or any other extension
                            document.body.appendChild(link);
                            link.click();
                        }
                    )
            } else {
                this.$alert("Please choose a stocktake list.");
            }
        },
        checkview(mode){
            if(this.view_mode=='all'){
                return true;
            } else {
                switch(this.view_mode){
                    case 'completed':
                        if(mode==true){
                            return true;
                        } else {
                            return false;
                        }
                    break;
                    case 'pending':
                        if(mode==false){
                            return true;
                        } else {
                            return false;
                        }
                    break;
                }
            }
        },
        fetch_stock_take_list(){
            var formData = new FormData();
            formData.append('rid',this.selected_record);
            axios.post("inventory/web/backend/fetch_stocktake_list.php",formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.datalist = response.data.data;
                        } else {
                            this.datalist = [];
                        }
                    }
                )
        },
        async fetch_stocktake_records(){
            await axios.get("inventory/web/backend/fetch_generated_list.php")
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.record_list = response.data;
                            this.selected_record = '';
                        } else {
                            this.record_list = [];
                        }
                    }
                )
            await this.fetch_stock_take_list()
        },
        return_pg(){
            this.$router.go(-1);
        },
        resetCount(){
            this.percentage=100;
            this.count_items();
        },
        count_full(){
            var formData = new FormData();
            formData.append('section',this.section);
            formData.append('grp_code',this.grpcode);
            formData.append('type','full');
            formData.append('percentage',100);
            axios.post('inventory/web/backend/count_items.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.item_count = response.data;
                        } else {
                            this.$alert("No records found").then(
                                ()=>{
                                    this.item_count = 0;
                                }
                            )
                        }
                    }
                )
        },
        count_items(){
            var formData = new FormData();
            formData.append('section',this.section);
            formData.append('grp_code',this.grpcode);
            formData.append('type',this.checktype);
            formData.append('percentage',this.percentage);
            axios.post('inventory/web/backend/count_items.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.item_count = response.data;
                            if(this.item_count<11){
                                this.checktype='full';
                                this.count_full();
                            }
                        } else {
                            this.$alert("No records found").then(
                                ()=>{
                                    this.item_count = 0;
                                }
                            )
                        }
                    }
                )
        },
        async load_filter(){
            var formData = new FormData();
            formData.append('section',this.section);
            await axios.post('inventory/web/backend/filter_preload.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.grplist = response.data.grp_code;
                            this.grpcode = '';
                        }
                    }
                )
            this.count_items();
        },
        select_mode(mode){
            this.mode = mode;
        },
        display_title(){
            if(this.mode=='view'){
                return 'View Stocktake List';
            }
            if(this.mode=='gen'){
                return 'Generate Stocktake List';
            }
        },
        createList(){
            if((this.period.start!='')&&(this.period.end!='')){
                var formData = new FormData();
                formData.append('dp',this.description);
                formData.append('section',this.section);
                formData.append('grp_code',this.grpcode);
                formData.append('type',this.checktype);
                formData.append('percentage',this.percentage);
                formData.append('item_count',this.item_count);
                formData.append('st',this.period.start);
                formData.append('end',this.period.end);
                var msg = "Confirm Generating Stocktake list ?";
                this.$confirm(msg).then(
                    ()=>{
                        axios.post("inventory/web/backend/gen_stocktake_list.php",formData)
                            .then(
                                (response)=>{
                                    if(response.data=="OK"){
                                        this.$alert("Successfully generated stocktake list").then(
                                            ()=>{
                                                this.fetch_stocktake_records();
                                                this.section='';
                                                this.grpcode='';
                                                this.period.start = '';
                                                this.period.end = '';
                                                this.item_count = 0;
                                                this.percentage = 100;
                                                this.checktype = 'full';
                                                this.mode = 'view';
                                            }
                                        )
                                    }
                                }
                            )
                        }
                    )
            } else {
                this.$alert("Please pick the dates");
            }
        }
    }
}
</script>
<style scoped>
input[type='radio']{
    margin-top:0.25rem;
}
.rowWrapper{
    margin-top:1rem;
    margin-bottom: 1rem;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.flex{
    display:flex;
    flex-direction:row
}
.tabs{
    position:absolute;
    bottom:0;
    height:40px;
    left:0;
    right:0;
}
.leftTab{
    text-align:left;
    padding-left:20px;
    left:0;
    right:50%;
    border-top-right-radius: 35px;
}
.rightTab{
    text-align:right;
    padding-right:20px;
    right:0;
    left:50%;
    border-top-left-radius: 35px;
}
.subTabs{
    font-weight: bold;
    padding-top:5px;
    position:absolute;
    bottom:-2px!important;
    background-color:#FFFFFF;
    height:35px;
    opacity:0.4;
    cursor:pointer;
    transition: 0.3s;
    transition-property: opacity;
}
.activeTab{
    opacity:1!important;
    z-index: 10;
}
.leftTab.activeTab{
    right:45%;
}
.rightTab.activeTab{
    left:45%;
}
.bodyWrapper{
    margin-top:10px;
    padding-left:8px;
    padding-right:8px;
    opacity:0;
    transition: 0.3s;
}
.disableTab:hover{
    opacity: 0.65;
}
</style>


